import React from 'react'
import Link,{navigateTo} from 'gatsby-link'


const Start = ({
  startTitleSmall,
  startTitleSpan,
  startTitleStrong
}) => (
  <div id="email-life-cycle" className="section-start section-sep">
    <div className="container-fluid">
      <div className="col-xs-12">
        <h3>
          <small>
            {startTitleSmall}
          </small>
          {startTitleSpan} 
          <br/> 
          <strong>{startTitleStrong}</strong>
        </h3>
      </div>
    </div>  
  </div>
)


export default Start



