import React from 'react'
import Link,{navigateTo} from 'gatsby-link'

import Hero from "./body/Hero"
import Start from "./body/Start"
import Timeline from "./body/Timeline"

const Body = ({isMobile, theme}) => (
  <div id="body-content" className={`site-body-content site-body-${theme.siteTheme}`}>
    <Hero {...theme}/>
    <Start {...theme}/>
    <Timeline isMobile={isMobile} {...theme}/>
  </div>
)

export default Body


