import React,{Component} from 'react'
import PropTypes from 'prop-types'
import Helmet from "react-helmet"
import Link,{navigateTo} from 'gatsby-link'
import config from "../../data/SiteConfig"
import {IS_DEV, CURRENT_THEME} from "../../data/constants"
import themes from "../../data/Themes"

import "../assets/styles/fontawesome-all.min.css"
import "../assets/styles/index.scss"

import Header from '../components/Header'
import Body from '../components/Body'
import Footer from '../components/Footer'
import {SidebarLeft,SidebarRight} from '../components/Sidebar'

const theme = themes[CURRENT_THEME]

class Layout extends Component {

  constructor(props) {
    super(props)
    this.state = {
      isMobile: false
    } 
  }

  onToggleMobile() {
    const {isMobile} = this.state
    this.setState({isMobile:!isMobile})
  }

  render() {
    const {
      siteTheme,
      siteUrl,
      siteCover,
      siteTitle,
      siteSlogan,
      items = []
    } = theme
    return (
      <div>
        <Helmet>
          <title>{`${config.siteTitle}`}</title>
          <meta name="description" content={config.siteDescription} />
          <meta name="app" content="catalog"/>
        </Helmet>
        <SidebarLeft {...this.state} self={this} theme={theme}/>
        <SidebarRight {...this.state} self={this} theme={theme}/>
        <main id="main" className={`site-main site-${CURRENT_THEME}`}>
          <Header {...this.state} self={this}/>
          <Body {...this.state} self={this} theme={theme}/>
          <Footer {...this.state} self={this} theme={theme}/>
        </main>
      </div>
    )
  }

  scroll(to) {
    if (goTo) {
      goTo(to)
    }
  }

  componentWillMount() {}
  componentDidMount() {}
  componentWillUnmount() {}
}

Layout.propTypes = {
  children: PropTypes.func,
}

export default Layout

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`


