import React from 'react'
import Link,{navigateTo} from 'gatsby-link'

const SidebarLeft = ({
  self,
  isMobile = false
  }) => (
  <div id="sidebar" className="site-sidebar site-sidebar-left">

    <div className="collection-colors">
    
      <div className="single-thumb-toggle hidden-xs">
        <div 
        onClick={() => self.onToggleMobile()}
        className={!isMobile?'selected':''}>
          <i className="far fa-desktop"/>
        </div>
        <div 
        onClick={() => self.onToggleMobile()}
        className={isMobile?'selected':''}>
          <i className="far fa-mobile-android"/>
        </div>
      </div>

      <a title="View User Welcome 404 Pink" href="/email/user-welcome-404-pink/">
        <span className="" style={{backgroundColor: 'rgb(255, 154, 190)'}}/>
      </a>
      <a title="View User Welcome 404 Gold" href="/email/user-welcome-404-gold/">
        <span className="" style={{backgroundColor: 'rgb(226, 175, 57)'}}/>
      </a>
      <a title="View User Welcome 404 Purple" href="/email/user-welcome-404-purple/">
        <span className="" style={{backgroundColor: 'rgb(88, 40, 119)'}}/>
      </a>
      <a title="View User Welcome 404 Blue" href="/email/user-welcome-404-blue/">
        <span className="" style={{backgroundColor: 'rgb(0, 123, 226)'}}/>
      </a>
      <a title="View User Welcome 404 Orange" href="/email/user-welcome-404-orange/">
        <span className="" style={{backgroundColor: 'rgb(239, 117, 38)'}}/>
      </a>
      <a title="View User Welcome 404 Red" href="/email/user-welcome-404-red/">
        <span className="" style={{backgroundColor: 'rgb(194, 48, 7)'}}/>
      </a>
      <a title="View User Welcome 404 Green" href="/email/user-welcome-404-green/">
        <span className="active" style={{backgroundColor: 'rgb(0, 150, 136)'}}/>
      </a>
      <a title="View User Welcome 404 Light" href="/email/user-welcome-404-light/">
        <span className="" style={{backgroundColor: 'rgb(233, 233, 233)'}}/>
      </a>
    </div>
  </div>
)




const SidebarRight = ({theme, self}) => (
  <div id="sidebar" className="site-sidebar site-sidebar-right">
    {theme.items.map(({id,thumbnailSM,title},i) => {
      return (
        <div key={i} onClick={() => self.scroll(id)}>
          <img src={thumbnailSM} className="img-responsive"/>
        </div> 
      )
    })}
  </div>
)



export {SidebarLeft,SidebarRight}



