import React from 'react'
import Link,{navigateTo} from 'gatsby-link'
import config from "../../data/SiteConfig"

const Header = ({}) => (
  <div id="header" className="site-header">
    <div className="logo">
      <a href="#">
        <img src={config.logo} className="img-responsive"/>
      </a>
    </div>
  </div>
)

export default Header



