import React from 'react'
import Link,{navigateTo} from 'gatsby-link'

import FacebookProvider, { Like, Share } from 'react-facebook'
//import ShareLinkedin from 'react-share-linkedin'
//import { Tweet } from 'react-twitter-widgets'
import GitHubButton from 'react-github-button'

const IPHONE_X    = "https://firebasestorage.googleapis.com/v0/b/mailcolbri-studio.appspot.com/o/public%2Fweb%2FIPHONE_X.png?alt=media&token=ea740316-99b4-4bcb-bb01-a4c074541278"
const IPAD_GOLD   = "https://firebasestorage.googleapis.com/v0/b/mailcolbri-studio.appspot.com/o/public%2Fweb%2FIPAD_GOLD.png?alt=media&token=730f2e0d-712d-4b39-a437-ce4c6413dc84"

const ItemDetails = ({
  title,
  description,
  urlEdit,
  urlCode
}) => (
  <div className="item-details">
    <h2>
    <small>Email Template</small>
    <span>{title}</span>
    </h2>
    <p>{description}</p>
    <div className="actions">
      <a href={urlEdit} title={`${title}`} className="button button-live hidden-xs">
        <i className="fas fa-play-circle"/>
        Live Edit
      </a>
      <a href={urlCode} title={`${title}`} className="button button-source">
        <i className="far fa-code"/>
        Source
      </a>
    </div>
  </div>
)

const ItemThumbLG = ({
  title,
  thumbnail,
  urlEdit,
  urlCode
}) => (
  <div className="item-thumb" style={{backgroundImage:`url(${IPAD_GOLD})`}}>
    <div className="item-scroll">
      <a href={urlEdit} title={`${title}`}>
        <img src={thumbnail} className="img-responsive"/>
      </a>
    </div>
  </div>
)

const ItemThumbXS = ({
  title,
  thumbnailMobile,
  urlEdit,
  urlCode
}) => (
  <div className="item-thumb-mobile" style={{backgroundImage:`url(${IPHONE})`}}>
    <div className="item-scroll">
      <a href={urlEdit} title={`${title}`}>
        <img src={thumbnailMobile} className="img-responsive"/>
      </a>
    </div>
  </div>
)

const ItemThumbXS2 = ({
  title,
  thumbnailMobile,
  urlEdit,
  urlCode
}) => (
  <div className="item-thumb-mobile-x">
    <div className="item-phone-x" style={{backgroundImage:`url(${IPHONE_X})`}}/>
    <div className="item-scroll-x">
      <a href={urlEdit} title={`${title}`}>
        <img src={thumbnailMobile} className="img-responsive"/>
      </a>
    </div>
  </div>
)



const Timeline = ({isMobile, items = []}) => (
  <div className={`section-timeline section-sep ${isMobile?'section-timeline-mobile':''}`}>
    <div className="container-fluid reset">
      <div className="col-xs-12 reset">
        <div className="timeline">

          <div className="item-target">
            <i className="fas fa-chevron-circle-down"/>
          </div>

          <div className="timeline-nav">
            <div className="timeline-nav-content">
              <div className="nav-group">
                <h4>Templates</h4>
                <div className="nav-item">
                  <a href="#">
                    Welcome Template
                  </a>
                </div>
                <div className="nav-item">
                  <a href="#">
                    User Comfirm Template
                  </a>
                </div>
                <div className="nav-item">
                  <a href="#">
                    User Receipt Template
                  </a>
                </div>
                <div className="nav-item">
                  <a href="#">
                    User Invoice Template
                  </a>
                </div>
              </div>

              <div className="nav-group">
                <h4>Share</h4>
                <div className="social-item">
                  <GitHubButton 
                  type="stargazers" 
                  size="large" 
                  namespace="benjycui" 
                  repo="react-github-button" 
                  className="github-button"
                  />
                  <i className="fab fa-github"/>
                </div>
                <div className="social-item">
                  <FacebookProvider appId="230595630848424">
                    <Like 
                    href="http://www.facebook.com" 
                    colorScheme="dark" 
                    showFaces={false}
                    share={true}
                    layout="button_count" 
                    size="large" 
                    />
                  </FacebookProvider>
                </div>
              </div>

              <div className="nav-group nav-group-social">
                <a className="tw" href="#">
                  <i className="fab fa-twitter"/>
                </a>
                <a className="fb" href="#">
                  <i className="fab fa-facebook-f"/>
                </a>
                <a className="git" href="#">
                  <i className="fab fa-github"/>
                </a>

              </div>
            </div>
          </div>


          <div className="items row reset">
            {items.map((item,i) => {
              return (
                <div id={item.id} key={i} className="col-xs-12 reset">
                  <div className={`item item-${i} item-${i%2?"even":"odd"}`}>
                    <div className="item-content">
                      <ItemDetails {...item}/>
                      {isMobile
                        ?(<ItemThumbXS2 {...item}/>)
                        :(<ItemThumbLG {...item}/>)}
                    </div>
                  </div>
                </div>  
              )
            })}
          </div>


        </div>
      </div>
    </div>  
  </div>
)

/*

            <ItemThumbXS {...item}/>
            <StickyContainer>
              <Sticky>
                <div className="dv"/>
              </Sticky>
            
            </StickyContainer>
*/


// <Masonry updateOnEachImageLoad={true}></Masonry>




export default Timeline




