import React from 'react'
import Link,{navigateTo} from 'gatsby-link'
import config from "../../data/SiteConfig"

const Footer = ({theme}) => (
  <div id="footer" className="site-footer section-sep">
    <div className="item-target">
      <i className='fas fa-chevron-circle-down'/>
    </div>
    <div className="container-fluid">
      <div className="col-xs-12">
        <div className="content">
          <h3>
            {theme.footerTitle} 
          </h3>
          <div className="actions">
            <a className="action-github" target="_blank" href={theme.footerBtnDownloadUrl}>
              <i className="fab fa-github"/>
              {theme.footerBtnDownloadLAbel}
            </a>
            <a className="action-catalog" target="_blank" href={theme.footerBtnEditUrl}>
              <img src={config.logoWhite} className="img-responsive"/>
              {theme.footerBtnLiveEditLAbel}
            </a>
          </div>
        </div>
      </div>
    </div>  
    <div className="cover" style={{
      backgroundImage: `url(${theme.footerCover})`
    }}/>
  </div>
)




export default Footer


